<template>
  <div class="md-render-wrapper">
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'md-render',
  data() {
    return {}
  },
  mounted() {
    this.renderMdFile()
  },
  watch: {
    '$route.params.filename': function () {
      this.renderMdFile()
    },
  },
  methods: {
    renderMdFile() {
      import(/* webpackChunkName: "marked-share" */ 'marked').then((marked) => {
        axios({
          method: 'get',
          url: `http://padmedia.oss-cn-beijing.aliyuncs.com/support/${this.$route.params.filename}`,
          headers: { pragma: 'no-cache', 'cache-control': 'no-cache' },
        }).then((response) => {
          let markedHtml = marked(response.data)
          const regex = /<img.*?src="(.*?).mp4".*?(\/|)>/igm
          markedHtml = markedHtml.replace(regex, '<video src="$1.mp4" controls="controls" controlslist="nodownload" poster="$1.png" preload="metadata"></video>')
          const titleRegex = /<h1.*?>(.*?)<\/h1>/igm
          const match = titleRegex.exec(markedHtml)
          document.title = match ? match[1] : '沃未来派 - 用户支持'
          this.$el.innerHTML = markedHtml

          import(/* webpackChunkName: "marked-share" */ 'weixin-js-sdk').then((wx) => {
            axios.get('/pad/j/signature/wechat', { params: { url: window.location.href } }).then((wxRes) => {
              const signature = wxRes.data.wechatSignature
              wx.config({
                debug: false,
                appId: 'wx03bc90494a827de6',
                timestamp: signature.timestamp,
                nonceStr: signature.nonceStr,
                signature: signature.signature,
                jsApiList: ['onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'onMenuShareWeibo', 'onMenuShareQZone'],
              })

              const shareIcon = require('assets/img/share-icon.png')
              const shareParams = {
                title: document.title,
                link: window.location.href,
                imgUrl: `${window.location.protocol}//${window.location.host}${shareIcon}`,
              }
              wx.ready(() => {
                wx.onMenuShareTimeline({ ...shareParams })

                wx.onMenuShareAppMessage({
                  desc: '沃未来派 - 用户支持',
                  ...shareParams,
                })

                wx.onMenuShareQQ({
                  desc: '沃未来派 - 用户支持',
                  ...shareParams,
                })

                wx.onMenuShareWeibo({
                  desc: '沃未来派 - 用户支持',
                  ...shareParams,
                })
                wx.onMenuShareQZone({
                  desc: '沃未来派 - 用户支持',
                  ...shareParams,
                })
              })
            })
          })
        })
      })
    },
  },
}
</script>

<style lang="less">
@import "~assets/css/base-variables.less";

.md-render-wrapper {
  max-width: 740px;
  font-size: 14px;
  padding: .5rem;
  line-height: 1.5;
  margin: 1rem auto;
  font-family: @base-font-family;
  color: @font-color-base;
}

h1, h2, h3, h4 {
  margin-top: 1em;
  margin-bottom: .5em;
  font-weight: 400;
}

h1 {
  font-size: 2rem;
  padding-bottom: .5rem;
  border-bottom: solid 1px #ddd;
  margin-bottom: 1rem;
}

h2 {
  font-size: 1.8rem;
}

h3 {
  font-size: 1.6rem;
}

h4 {
  font-size: 1.2rem;
}

h5 {
  font-size: 1rem;
}

h5 {
  font-size: .8rem;
}

strong {
  color: @color-red;
}

a {
  color: @color-primary;
  text-decoration: none;
  font-weight: normal;
  margin-right: .5rem;
  border-radius: 2px;
  padding: 2px;
}
a:after {
    content: '>>';
}

a:hover, a:focus, a:active {
  background-color: @color-primary;
  color: white;
}

hr {
  height: 0;
  margin: 15px 0;
  overflow: hidden;
  background: transparent;
  border: 0;
  border-bottom: 1px solid #ddd;
}

img {
  display: block;
  margin: 1rem auto;
  max-width: 95%;
  border-radius: 0.3em;
  box-shadow: rgba(0, 0, 0, .3) 0 1px 4px;
  box-sizing: border-box;
}

video {
  display: block;
  margin: 1rem auto;
  max-width: 95%;
  box-shadow: rgba(0, 0, 0, .3) 0 1px 4px;
}

code {
  display: inline-block;
  white-space: no-wrap;
  background: #eee;
  font-size: 1rem;
  line-height: 1.2em;
  color: #00897b;
  border-radius: 0.3em;
  padding: 0 .3em;
  margin: -1px 4px;
}


</style>



